<template>

    <div class="slide"  >
      <img v-bind:src="slide" v-bind:class="animate"/>
    </div>
</template>

<script>
    export default {
        data() {
            return {}
        },
        props: ['slide','animate']
        
    }
    
</script>

<style>
    .slide {
        height: 500px;
        overflow: hidden;
    }
    img {
        width:100%;
    }
</style>


