
<template>
  <main class="main">

        <section class="section">
            <div class="container">
                <div class="row">
                    <div class="col-xs-12">
                        <h2 class="section__title"><strong>Photo Slide</strong> </h2>
                    </div>
                </div>

                <div class="row">
                    <div class="col-xs-12">
                         <div  v-for="number in [slideImages.currentNumber]" >
                            <Slide v-bind:slide = "slideImages.images[Math.abs(slideImages.currentNumber) % slideImages.images.length]" 
                              v-bind:animate="slideImages.amimates[Math.abs(slideImages.currentNumber) % slideImages.images.length]"
                            />                                                       
                          </div>                      
                    </div>
                </div>             
            </div>
        </section>
    </main>
    <!-- /.main -->
</template>

<script>
  import Slide from './Slide'
  export default {
    data () {
      return {
         slideImages: {
            images: ['http://i.imgur.com/vYdoAKu.jpg', 'http://i.imgur.com/PUD9HQL.jpg', 'http://i.imgur.com/Lfv18Sb.jpg', 'http://i.imgur.com/tmVJtna.jpg', 'http://i.imgur.com/ZfFAkWZ.jpg'],
            currentNumber: 0,
            timer: null,
            amimates: [             
             'animated tada',
             'animated rotateIn',
             'animated fadeIn',
             'animated slideInLeft',
             'animated pulse'
            ]
        }
      }
    },
  
    beforeMount(){
        this.startRotation();
    },

    components: {
      Slide
    },
    methods: {
        startRotation: function() {
            this.slideImages.timer = setInterval(this.next, 3000);
        },
        next: function() {
            this.slideImages.currentNumber += 1
            console.log("next")
        },
        prev: function() {
            this.slideImages.currentNumber -= 1
        }
    }
   
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" type="text/scss">  
</style>



